import { useMemo } from 'react';

import './WrapperMain.scss';

const WrapperMain = ({classes, children}) => {

	const wrapperClasses = useMemo(() => {
		return `wrapper-main ${classes}`;
	}, [classes]);

	return (
		<div className={wrapperClasses}>
			{children}
		</div>
	);
};

export default WrapperMain;
